.posts {
  padding:20px 0;
  border-bottom: 1px solid rgb(143, 24, 43);
  margin-right:2%; 

  h1{
    font-size: 18px;
  }
}

//postContent.js
.post-view {
  margin-top: -15px;

  small {
    font-size:15px;
  }

  h1{
    font-size: 15px;
  }

  .comment-button{
    padding:0 10px;
    margin: 0;
  }

  .date-text{
    font-size: 12px;
  }

  .post-icon{
    width:70px;
    height: 70px;
    border-radius: 50px;
    margin-right: 15px;
  }
  
  .post-content{
    font-size: 25px;
    margin:10px 0;
  }

  .post-image {
    width: 100%;
    opacity: 0.8 ;
    &:hover {
      opacity: 1;
    }
  }

  .post-interactions {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }

}

.post-settings {

  justify-content: space-between;

  .settings-icon {
    position: relative;
    width:10%;
    justify-content: flex-end;
  }

  .post-delete{
    margin-top:20%;
    position: absolute;
    padding:0 0.3em;
  }

}

//postRatingStars.js
.post-stars{
display: flex;
justify-content: flex-end;
}

//MakePost.js
.make-post-container {
  background-color:rgba(255,255,255, 0.3);
  padding: 20px;
  border-radius: 20px;
  margin:0 0 10px 0 ;

  .post-form{
    width: 100%;
    }
  }

  .post-texarea{
    width: 90%;
    border-radius: 10px;
  }

  .post-button {
    height:30px;
    margin: 10px;
    width: 6vw;
  }

  .camera-button{
    width:2vw;
    margin-bottom:0.5vh;
  }



//postComments.js
.post-comments{ 
  font-size: 20px;
  background-color: rgba(255,255,255, 0.6);
  padding:5px;
  border-radius: 10px;
  margin:1% 0 ;

  small{
    font-size: 11px;
  }

  .comment-details{
    display: flex;
    justify-content: space-between;
  }
}

// makeComments.js

.make-comment {
  padding: 1%;
}
.comment-content{
display: flex;
justify-content: space-between;
padding:2px;
  small{
    margin:2px;
  }
}

.comment-input{
  width:75%;
  height:20px;
  border-radius: 5px;
  margin: 10px 0;
}


.ratings-modal {
overflow-y: scroll;
height: 70vh;
}
